import React from "react";

const Doctrines = () => {
  return (
    <>
      <h2>Doctrines</h2>
      <p>
        Nirmal Kutia Johalan was established by Sriman Sant Baba
        Basant Singh ji Maharaj. Sant ji Maharaj belonged to the
        Nirmala faction of Sikh Saints. His Gurudev Sriman Sant Baba
        Harnam Singh ji Maharaj of Jiyan was the disciple of Sriman
        Sant Baba Karam Singh ji Maharaj of Hoti Mardan. Strictly
        speaking, Sant Baba Basant Singh ji was from the lineage of
        Hoti Mardan Saints.
      </p>
      <p className="my-5">
        Sant Baba Harnam Singh ji Maharaj graced his loving disciple
        Sant Baba Basant Singh ji with the blissful wisdom of the Lord
        and enlightened him generously with divine knowledge. Sant
        Baba Basant Singh ji with the blessings of his Gurudev,
        remained in deep meditation and performed extreme worship of
        the Almighty. He soon became an achieved soul with the
        knowledge of the deep mysteries of the Creator and His
        Creation. Sant Baba Harnam Singh ji transferred his entire
        austerity to Sant Baba Basant Singh ji and predicted that the
        pious soul would have a large following and would be respected
        by Rajas and Maharajas. Gurudev blessed him and said, “Santa!
        Your penance is complete in this abode. You should now tackle
        the ignorance and misery of common people. You should bless
        individuals with the teachings of Dhan Dhan Sri Guru Granth
        Sahib ji and administer the Nectar of the two edged sword.”
      </p>
      <p>
        Sant Baba Basant Singh ji obeyed the orders of his Gurudev.
        Sant Baba ji performed his meditation in the Name of the
        Almighty at different places preaching people to take refuge
        in Gurbani and baptized them with the Amrit(Nectar) of the
        Khanda Bata. Leading the sangat to edification and
        deliverance, Sant Baba ji finally reached village Johal, near
        Jalandhar Cantonment. Blessed was the soil of village Johal
        which became home to this great ascetic, selfless soul and the
        epitome of spirituality. Amidst the wasteland, the great soul,
        imbued in Godliness, took on the Holy task of upliftment of
        mankind.
      </p>
      <p className="my-5">
        The present abode of Nirmal Kutia was graced by him for more
        than fifty years. He left a legacy of piousness and peace at
        the place in 1971.
      </p>
      <p>
        During his lifetime, Sant Baba Basant Singh ji Maharaj was
        involved in bringing about changes in individuals as directed
        by his Gurudev. Briefly, the following were some of the
        activities he performed:
      </p>
      <ol className="my-5">
        <li>Distributing teachings of Gurbani;</li>
        <li>
          Encouraging youngsters to recite Gurbani Kirtan, Vaars
          and Holy speeches;
        </li>
        <li>Teaching individuals the path of divine;</li>
        <li>Administering Amrit to the seekers;</li>
        <li>
          Educating people in Selfless service and Imparting the
          doctrine of Simran.
        </li>
        <li>Works on social welfare;</li>
        <li>Environmental cleanliness;</li>
      </ol>
      <p>
        This generous work was carried on and expanded during the
        times of Sriman Sant Baba Gian Singh ji Maharaj, the devoted
        disciple of Sant Baba Basant Singh ji, who held the reins of
        the Kutiya from 1971 to 2001 and then by Sriman Sant Baba
        Harbhajan Singh ‘Virakat’ ji Maharaj till March 2002. The
        works of Sant Baba Basant Singh ji are being administered in
        the same spirit, light and essence by the present head of the
        Kutiya, Sant Baba Jit Singh ji. He is ably assisted in his
        efforts by Sant Baba Gurmeet Singh ji.
      </p>
      <p className="my-5">
        The following aspects of Nirmal Kutiya Johalan outline the
        teachings and philosophy of the Holy Saints:
      </p>
      <ol>
        <li>
          The Holy Saints encourage individuals to be regular in
          joining the Sadh Sangat. Sadh Sangat is basically a place
          where only the Name of the Almighty is taken. Such a Sangat
          brings about strong commitment in oneself. This aspect is
          strongly recommended in the Sikh religion. Sikhism preaches
          congregational involvement and collective movement as an
          attempt towards shared humanism and spiritual strength;
        </li>
        <li>
          Emphasis is on a truthful and honest living by
          individuals; 3. There is strong opposition to superstitions
          normally found in village folk;
        </li>
        <li>
          There is strong opposition to superstitions normally
          found in village folk;
        </li>
        <li>
          Every individual should live within the divine order. If
          he does so then the individual is able to face the comforts
          and anxieties/difficulties of life in equal measure;
        </li>
        <li>
          The baptismal initiating ceremony (Accepting Amrit
          prepared by the five beloved in a Khanda-Bata) is meant as
          the climactic benediction of the tenth Guru Sahib Sri Guru
          Gobind Singh ji Maharaj and should be accepted by the
          devotees;
        </li>
        <li>
          Amrit is the common bond of brotherhood in the service of
          human relationship;
        </li>
        <li>
          The seeker after initiation becomes free from the
          distinctions of gender, caste, color and social status;
        </li>
        <li>
          One who follows the discipline of Baani (Holy Hymns) and
          Baana (The Dress Code) is a respected individual;
        </li>
        <li>
          It is necessary to read, recite and sing the verses of
          Sri Guru Granth Sahib ji with reverence. The meaning of the
          verse has to be absorbed to enjoy its beauty;
        </li>
        <li>
          The Nitnem Baanis shiuld be remembered by heart. The
          Baanis can then be recited even when one is on duty or is
          not in a position to read it from the Holy Gutka Sahib. The
          discipline does not get disturbed.
        </li>
        <li>
          Every Gursikh in service, whether civilian or uniformed
          should perform his duties with honesty and hard work,
          respect his seniors and not get tangled in corrupt
          practices. Gurbaani in mind and fear of Satguru, will help
          in leading a simple truthful life, clean one's conscience
          and accord clarity of work in hand. Patience, forgiveness,
          tolerance, contentment, restraint of mind and senses,
          compassion and wisdom will automatically flow from within in
          such individuals.
        </li>
        <li>
          Truthful living within the precincts of Gursikhi lead
          not only self to reach newer heights but the children/family
          of such individuals ascend spiritually as well as
          materialistically.
        </li>
        <li>
          The grace of achieved sould is sufficient for changing
          minds, bring in purity and mould people to follow Guru's
          path.
        </li>
        <li>
          As the Baani (Holy Hymns) is imbibed more deeply,
          humility surfaces.
        </li>
        <li>
          Wish goodwill for all, as it is a part of the Sikh
          prayer;
        </li>
        <li>
          A disciplined spiritual life should be the endeavor;
        </li>
        <li>
          The life of a common person is spent unconsciously. They
          pass a life full of misery, anguish, anxiety, worries and
          fretfulness. They take up drugs and alcoholic drinks to
          drown their miseries, leading them further into
          unconsciousness. People have to be gently brought out from
          these concerns, to understand their folly and lead a
          peaceful life for the benefit of mankind. Selfless service
          cannot be taught. It can only be observed being undertaken
          by others and get motivated to follow the same;
        </li>
        <li>
          Following one’s own religion in the right perspective
          makes one confident and committed to its roots. The elders
          must lead by example;
        </li>
        <li>
          Young minds are more prone and sensitive to get easily
          distracted from the true path. They do not understand that
          once they are uprooted, they cannot be any greenery for
          them. It is therefore necessary for the parents to guide
          them. An awakened soul is always helpful in such times;
        </li>
        <li>
          Greed is thinking about a better future. Life is a
          blessing by the Almighty. It is so beautiful but we have
          destroyed it by remaining greedy not knowing how to live the
          life in the present;
        </li>
        <li>
          The Almighty can be observed through love. Love received
          from the family is like water fetched from a well. The real
          need is the downpour of rain water from the Almighty to
          quench the thirst collected over many births;
        </li>
      </ol>
      <p className="mt-5">
        There is no end to the teachings. Everyday a fresh one is
        there. Individuals, who ensure following just one teaching,
        become achieved souls.
      </p>
    </>
  );
};

export default Doctrines;
