import React, { useRef, useState } from 'react'
import { ROUTES } from '../../../../utils/constants';
import styles from "./Sidebar.module.scss";
import { NavDropdown } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

const Sidebar = () => {
    const navLinks = [
        {
            label: "Home",
            to: ROUTES.HOME,
        },
        {
            label: "Background",
            links: [
                {
                    label: "The Saints",
                    to: ROUTES.BACKGROUND.SAINTS,
                },
                {
                    label: "Nirmalas",
                    to: ROUTES.BACKGROUND.NIRMALAS,
                },
                {
                    label: "Nirmal Bhekh",
                    to: ROUTES.BACKGROUND.NIRMAL_BHEKH,
                },
            ]
        },
        {
            label: "Introduction",
            links: [
                {
                    label: "About Nirmal Kutiya Johlan",
                    to: ROUTES.INTRODUCTION.ABOUT,
                },
                {
                    label: "Johlan Village",
                    to: ROUTES.INTRODUCTION.JOHLAN_VILLAGE,
                },
                {
                    label: "Name of Kutiya",
                    to: ROUTES.INTRODUCTION.NAME_OF_KUTIYA,
                },
                {
                    label: "Main Buildings",
                    to: ROUTES.INTRODUCTION.MAIN_BUILDINGS,
                },
                {
                    label: "Guru Ka Langar",
                    to: ROUTES.INTRODUCTION.GURU_KA_LANGAR,
                },
                {
                    label: "Doctrines",
                    to: ROUTES.INTRODUCTION.DOCTRINES,
                },
            ]
        },
        {
            label: "Life Sketch",
            links: [
                {
                    label: "Sant Baba Harnam Singh Ji Maharaj",
                    to: ROUTES.LIFE_SKETCHS.BABA_HARNAM_SINGH_JI,
                },
                {
                    label: "Sant Baba Basant Singh Ji Maharaj",
                    to: ROUTES.LIFE_SKETCHS.BABA_BASANT_SINGH_JI,
                },
                {
                    label: "Sant Baba Gian Singh Ji Maharaj",
                    to: ROUTES.LIFE_SKETCHS.BABA_GIAN_SINGH_JI,
                },
                {
                    label: "Sant Baba Harbhajan Singh Ji Maharaj",
                    to: ROUTES.LIFE_SKETCHS.BABA_HARBHAJAN_SINGH_JI,
                },
                {
                    label: "Sant Baba Jit Singh Ji Maharaj",
                    to: ROUTES.LIFE_SKETCHS.BABA_JIT_SINGH_JI,
                },
                {
                    label: "Lineage",
                    to: ROUTES.LIFE_SKETCHS.LINEAGE,
                },
                {
                    label: "Anniversary Dates Of Saints",
                    to: ROUTES.LIFE_SKETCHS.ANNIVERSARY_DATES_OF_SAINTS.replace(":id", "braham-giani-sant-baba-harnam-singh-ji-maharaj"),
                },
            ],
        },
        {
            label: "Teachings",
            links: [
                {
                    label: "Teaching Of Baba Basant Singh Ji",
                    to: ROUTES.TEACHINGS.TEACHINGS_OF_BABA_BASANT_JI,
                },
                {
                    label: "Love for Gurbani",
                    to: ROUTES.TEACHINGS.LOVE_FOR_GURBANI,
                },
                {
                    label: "Love and Teaching for All",
                    to: ROUTES.TEACHINGS.LOVE_AND_TEACHINGS,
                },
                {
                    label: "Sweet Sermons",
                    to: ROUTES.TEACHINGS.SWEET_SERMONS,
                },
                {
                    label: "Principles of Gursikh Preaching",
                    to: ROUTES.TEACHINGS.PRINICIPLES,
                },
                {
                    label: "Making Earth a Little Pure",
                    to: ROUTES.TEACHINGS.MAKING_EARTH_LITTLE_PURE,
                },
            ]
        },
        {
            label: "Publications",
            links: [
                {
                    label: "Nirmal Basant Bahar in Punjabi(2006)",
                    to: ROUTES.PUBLICATIONS.NIRMAL_BASANT.replace(":id", "nirmal-basant-bahar-in-punjabi-2006"),
                },
                {
                    label: "Nirmal Basant Bahar in Punjabi(2007)",
                    to: ROUTES.PUBLICATIONS.NIRMAL_BASANT.replace(":id", "nirmal-basant-bahar-in-punjabi-2007"),
                },
                {
                    label: "Nirmal Basant Bahar in Punjabi(2009)",
                    to: ROUTES.PUBLICATIONS.NIRMAL_BASANT.replace(":id", "nirmal-basant-bahar-in-punjabi-2009"),
                },
                {
                    label: "Stories",
                    to: ROUTES.PUBLICATIONS.STORIES,
                },
                // {
                // 	label: "Articles by Members of Sangat",
                // 	links: [
                // 		{
                // 			label: "Nagar Kirtan at Jalandhar",
                // 			to: ROUTES.PUBLICATIONS.ARTICLES_BY_SANGAT_MEMBERS.NAGAR_KIRTAN_JALANDHAR,
                // 		},
                // 		{
                // 			label: "Jagriti Yatra",
                // 			to: ROUTES.PUBLICATIONS.ARTICLES_BY_SANGAT_MEMBERS.JAGRITIY_YATRA,
                // 		},
                // 		{
                // 			label: "Anniversary Nov. 2019 Celebrations",
                // 			to: ROUTES.PUBLICATIONS.ARTICLES_BY_SANGAT_MEMBERS.ANNIVERSARY_2019_CELEBRATION,
                // 		},
                // 	]
                // },
            ],
        },
        {
            label: "Gallery",
            links: [
                // {
                // 	label: "Audio",
                // 	to: ROUTES.GALLERY.AUDIO,
                // },
                {
                    label: "Videos",
                    to: ROUTES.GALLERY.VIDEOS,
                },
                {
                    label: "Photos",
                    to: ROUTES.GALLERY.PHOTOS,
                },
            ],
        },
    ];
    const ref = useRef(null);
    const [active, setActive] = useState(false);
    const handleActive = () => setActive(!active);
    const handleClick = () => {
        ref.current && ref.current.click();
        document.body.clientWidth < 1199 && handleActive();
    }
    return (
        <>
            <div className={`${styles.nav} ${active ? styles.active : ""}`}>
                {
                    navLinks.map(item => {
                        if (item.links) {
                            return (
                                <NavDropdown ref={ref} className={styles.dropdown} key={item.label} title={item.label}>
                                    {
                                        item.links.map(innerItem => {
                                            if (innerItem.links) {
                                                return (
                                                    <NavDropdown className={styles.inner_dropdown} key={innerItem.label} title={innerItem.label}>
                                                        {
                                                            innerItem.links.map(mostInnerItem => {
                                                                return (
                                                                    <NavLink onClick={handleClick} key={mostInnerItem.to} to={mostInnerItem.to}>
                                                                        {mostInnerItem.label}
                                                                    </NavLink>
                                                                )
                                                            })
                                                        }
                                                    </NavDropdown>
                                                )
                                            } else {
                                                return (
                                                    <NavLink onClick={handleClick} key={innerItem.to} to={innerItem.to}>
                                                        {innerItem.label}
                                                    </NavLink>
                                                )
                                            }
                                        })
                                    }
                                </NavDropdown>
                            )
                        }
                        return (
                            <NavLink key={item.to} to={item.to}>
                                {item.label}
                            </NavLink>
                        )
                    })
                }
            </div>
            <div onClick={handleActive} className={`${styles.nav_overlay} ${active ? styles.active : ""} d-xl-none`} />
            <button onClick={handleActive} className={`${styles.toggler} ${active ? styles.active : ""} d-xl-none`}>
                <span></span>
                <span></span>
                <span></span>
            </button>
        </>
    )
}

export default Sidebar