const AnniversaryDates = () => {
    const options = [
        {
            label: "Braham Giani Sant Baba Harnam Singh ji Maharaj",
            value: "braham-giani-sant-baba-harnam-singh-ji-maharaj",
        },
        {
            label: "Braham Giani Sant Baba Basant Singh ji Maharaj",
            value: "braham-giani-sant-baba-basant-singh-ji-maharaj",
        },
        {
            label: "Braham Giani Sant Baba Gian Singh ji Maharaj",
            value: "braham-giani-sant-baba-gian-singh-ji-maharaj",
        },
        {
            label: "Braham Giani Sant Baba Harbhajan Singh 'Virakat' ji Maharaj",
            value: "braham-giani-sant-baba-harbhajan-singh-ji-maharaj",
        },
    ]
    const [loading, setLoading] = useState(false);
    const params = useParams();
    const navigate = useNavigate();
    const [data, setData] = useState([]);
    useEffect(() => {
        setLoading(true);
        setData(annversaryDetails.filter(item => item.key === params.id)[0])
        setTimeout(() => {
            setLoading(false);
        }, 400);
    }, [params])
    return (
        <>
            <h2>Anniversary Dates of Saints</h2>
            <div className="filter">
                <Select
                    className={"ms-auto"}
                    options={options}
                    defaultValue={options.filter(item => item.value === params.id)[0]}
                    onChange={option => navigate(ROUTES.LIFE_SKETCHS.ANNIVERSARY_DATES_OF_SAINTS.replace(":id", option.value))}
                />
            </div>
            <h4 className='text-center my-5'>{data.title}</h4>
            <div className="table-responsive">
                <Table className="anniversary_table">
                    <thead>
                        <tr>
                            <th>English Calendar</th>
                            <th>Samvat Bikrami</th>
                            <th>6 Poh (English)</th>
                            <th>6 Poh (English)</th>
                        </tr>
                    </thead>
                    <thead>
                        <tr>
                            <th>Year</th>
                            <th>Year</th>
                            <th>Date</th>
                            <th>Day</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            loading ?
                                <>
                                    <tr>
                                        <td colSpan={4}>
                                            <Spinner />
                                        </td>
                                    </tr>
                                </>
                                :
                                data &&
                                data.dates &&
                                data.dates.map(item => (
                                    <tr key={item.key}>
                                        <td>{item.englishYear}</td>
                                        <td>{item.samvatBikrami}</td>
                                        <td>{item.pohEnglishDate}</td>
                                        <td>{item.phEnglishDay}</td>
                                    </tr>
                                ))
                        }
                    </tbody>
                </Table>
            </div>
        </>
    )
}

export default AnniversaryDates