import React from 'react'

const LoveforGurbani = () => {
    return (
        <>
            <h2>Love for Gurbani</h2>
            <ol>
                <li>
                    Sant Baba Basant Singh ji Maharaj was very fond of listening to Gurbani Kirtan. He formed kirtani jathas and would listen to their recital. There were instances when by he graced even such Sikhs who could not have dreamt of playing harmonium or the tabla. They learnt to perform kirtan without any hesitation. He also prepared groups of traditional bards, the ones who to the accompaniment of the tabor and the lute sang ballads and exposed the sangat to their history. Preachers were made proficient and erudite to speak on Gurbani and Sikh discipline.
                </li>
                <li>
                    Many a times when Akhand Paths were held at the Kutiya, Sant Baba Basant Singh ji Maharaj listened to the continuous recitation of 48 hours without moving from the place. The priests were cautious of reciting the correct Gurbani and gave importance only to Gurbani.
                </li>
                <li>
                    He started the practice of ceremonially carrying Dhan Dhan Sri Guru Granth Sahib ji on a flowered decked four wheeler, in a Nagar Kirtan procession in cities and villages, celebrating the Gurpurabs of the Gurus. He sat in attendance on the Holy Dhan Dhan Sri Guru Granth Sahib ji and performed the service of Chaur Sahib.
                </li>
                <li>
                    He gave special importance to the festival of Hola Mohalla of the Tenth Master and celebrated by taking out a procession from the Kutiya and passing through some of the nearby villages.

                </li>
                <li>
                    The importance of Gurbani was conveyed in Sunday congregations at the Kutiya. The sangat was motivated to listen to Kirtan of the hymns of Dhan Dhan Sri Guru Granth Sahib ji and involve in the selfless service of the sangat.
                </li>
                <li>
                    The stage Secretary was specifically asked to announce that the attendance in the House of the Lord is not marked for those who come to the congregation with a fickle mind or in fashionable clothes. The Guru only blesses the unpretentious, the meek and the simple being.
                </li>
            </ol>
            <h4>
                Many devotees of Sant Baba ji experienced the ecstasy in the prayers offered to Satguru and also the strength in the prayers performed by five Gursikhs.
            </h4>
        </>
    )
}

export default LoveforGurbani