import React from 'react'
import styles from "./UnusualHoly.module.scss"
import justImg from "../../../../assets/images/UNUSUAL-HOLY-SERVICE.jpg";

import { Container } from 'react-bootstrap'

const UnusualHoly = () => {
    return (
        <section className={styles.section}>
            <Container>
               <div className={styles._inner}>
               <h2>An Unusual Holy Service</h2>
                <p>
                    Holy preceptors in the world are like the flowing water of rivers. Throughout their life, they are involved in the welfare of the people and awakening the spiritual facet of the human beings. They spread love for the Almighty and teach the method of executing any service by performing it themselves.
                </p>
                <img src={justImg} alt="" />
               </div>
            </Container>
        </section>
    )
}

export default UnusualHoly