import React from "react";
import styles from "./DetailedView.module.scss";
import Button from "../../../common/Button/Button";
import { Container } from "react-bootstrap";
import imgOne from "../../../../assets/images/baba-ji/Baba-Basant-Ji.jpg";
import imgTwo from "../../../../assets/images/baba-ji/Baba-Gian-Singh-Ji.png";
import imgThree from "../../../../assets/images/baba-ji/Baba-Harbhajan-Singh-Ji.png";
import imgFour from "../../../../assets/images/baba-ji/Baba-Jeet-Singh-Ji.png";
import { ROUTES } from "../../../../utils/constants";
import { useNavigate } from "react-router-dom";

const DetailedView = () => {
	const data = [
		{
			pic: imgOne,
			head: "Braham Giani Sant Baba Basant Singh ji Maharaj",
			text: "Sant Baba Basant Singh ji Maharaj was born in village Marula, District Hoshiarpur in the year 1885 (Samvat 1932), to father Sardar Fateh Singh and mother Sardarni Chand Kaur. Marula was a small village, five kilometers from the town of Chabbewal, two kilometers from village Jiyan and situated to the north of the city of Hoshiarpur. The land of the village was sandy and covered with bushes, shrubs and hedges. The season of spring really landed in the village of Marula. When the child was named ‘Basant Singh’",
			link: ROUTES.LIFE_SKETCHS.BABA_BASANT_SINGH_JI,
		},
		{
			pic: imgTwo,
			head: "Braham Giani Sant Baba Gian Singh ji Maharaj",
			text: "Braham Giani 108 Sant Baba Gian Singh ji Maharaj was born in the year 1912 (Samvat 1969), in village Bambeli, Tehsil Mahilpur, District Hoshiarpur. Bhai Harnam Singh, father and Sardarni Prem Kaur, mother were the fortunate parents of this respected soul. The parents of Sant Baba Gian Singh ji were dedicated followers of Sikhism.At the young age of sixteen years, he renounced his home, parents and his entire friends and relatives and came to the Johalan Kutia. While at the Kutia he performed all kinds of work.",
			link: ROUTES.LIFE_SKETCHS.BABA_GIAN_SINGH_JI,
		},
		{
			pic: imgThree,
			head: "Braham Giani Sant Baba Harbhajan Singh ‘Virakat’ ji Maharaj",
			text: "Sant Baba Harbhajan Singh ji Virakat was born in village Marula in the year 1929. His father Sardar Sunder Singh and mother Sardarni Jit Kaur were fortunate to be blessed with the birth of a son. He was only seven years old when he was given as an offering at the feet of Sant Baba Basant Singh ji Maharaj, by his grandfather Sardar Jeen Singh and father Sardar Sunder Singh.",
			link: ROUTES.LIFE_SKETCHS.BABA_HARBHAJAN_SINGH_JI,
		},
		{
			pic: imgFour,
			head: "Sant Baba Jit Singh ji Maharaj",
			text: "Sant Baba Jit Singh ji was born in village Kishankot (near Sri Hargobindpur) Tehsil Batala, District Gurdaspur. His parents were fortunate to be blessed with such a spiritual soul. He studied in the school and cleared his matriculation examination. In the year 1975, Sant Baba Gian Singh ji Maharaj the chief of Nirmal Kutia Johalan took him under his fold. Sant Baba Jit Singh ji served his Gurudev Sant Baba Gian Singh ji and Sant Baba Harbhajan Singh ji Virakat with love and full faith. He remained close to Sant Baba Gian Singh ji from 1984 to 2001.",
			link: ROUTES.LIFE_SKETCHS.BABA_JIT_SINGH_JI,
		},
	];
	const navigate = useNavigate();
	return (
		<>
			<section className={styles.detailed_view}>
				<Container>
					{data.map((item, index) => (
						<div className={styles.detailed_view_box} key={index}>
							<div className={styles.img}>
								<img src={item.pic} alt="" />
							</div>
							<div className={styles.data}>
								<h2>{item.head}</h2>
								<p>{item.text}</p>
								<Button onClick={() => navigate(item.link)} text="Read More" />
							</div>
						</div>
					))}
				</Container>
			</section>
		</>
	);
};

export default DetailedView;
