import React from 'react';
import { Container } from 'react-bootstrap';
import map from "../../../assets/images/approach-map.jpg";
import styles from "./Contact.module.scss";


const Contact = () => {
    return (
        <>
            <section className={styles.contact}>
                <Container>
                    <div className={styles.contact_in}>
                        <h2>
                            Contact
                        </h2>
                        <div className="box">
                            <h3 className='mb-5'>
                                Nirmal Kutiya Johalan,
                                Village-Johal, PO-Bolina Doaba,
                                Distt- Jalandhar, Punjab,
                                India
                            </h3>
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d109045.06278457759!2d75.645837!3d31.340811!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x391a59c0444b31ab%3A0x456c2e4f12822458!2sNirmal%20Kutiya%20Johalan%20(Sant%20Baba%20Basant%20Singh%20Ji)!5e0!3m2!1sen!2sus!4v1704047256592!5m2!1sen!2sus"
                                width="600"
                                height="450"
                                allowfullscreen=""
                                loading="lazy"
                                referrerpolicy="no-referrer-when-downgrade"
                                style={{maxWidth: "100%"}}
                                title="map"
                            ></iframe>
                        </div>
                    </div>
                </Container>
            </section>
            <section className={styles.contact}>
                <Container>
                    <div className={styles.contact_in}>
                        <h2>Approach Map</h2>
                        <img src={map} alt="approach map" />
                    </div>
                </Container>
            </section>
        </>
    )
}

export default Contact