import React from "react";
import { RouterProvider, createBrowserRouter } from "react-router-dom";
import ContentLayout from "./components/Layouts/ContentLayout/ContentLayout";
import Homelayout from "./components/Layouts/Homelayout/Homelayout";
import NoGaurd from "./components/routes/NoGaurd";
import Bhekh from "./components/pages/Backgrounds/Bhekh/Bhekh";
import Nirmalas from "./components/pages/Backgrounds/Nirmalas/Nirmalas";
import Saints from "./components/pages/Backgrounds/Saints/Saints";
import ErrorPage from "./components/pages/ErrorPage/ErrorPage";
import Home from "./components/pages/Home/Home";
import AboutUs from "./components/pages/Introductions/About/AboutUs";
import Buildings from "./components/pages/Introductions/Buildings/Buildings";
import Doctrines from "./components/pages/Introductions/Doctrines/Doctrines";
import Johlan from "./components/pages/Introductions/Johlan/Johlan";
import Langar from "./components/pages/Introductions/Langar/Langar";
import Name from "./components/pages/Introductions/Name/Name";
import AnniversaryDates from "./components/pages/LifeSketch/AnniversaryDates";
import LifeSketch from "./components/pages/LifeSketch/LifeSketch";
import Lineage from "./components/pages/LifeSketch/Lineage";
import NotFoundPage from "./components/pages/NotFoundPage/NotFoundPage";
import NirmalBasantBahar from "./components/pages/Publications/NirmalBasantBahar/NirmalBasantBahar";
import Stories from "./components/pages/Publications/Stories/Stories";
import LoveAndTeachingsforAll from "./components/pages/Teachings/LoveAndTeachingsforAll";
import LoveforGurbani from "./components/pages/Teachings/LoveforGurbani";
import MakingEarthPure from "./components/pages/Teachings/MakingEarthPure";
import PrinciplesforGurshikhPreaching from "./components/pages/Teachings/PrinciplesforGurshikhPreaching";
import SweetSermons from "./components/pages/Teachings/SweetSermons";
import Teachings from "./components/pages/Teachings/Teachings";
import { ROUTES } from "./utils/constants";
import Photos from "./components/pages/Gallery/Photos";
import Audio from "./components/pages/Gallery/Audio";
import Videos from "./components/pages/Gallery/Videos";
import Contact from "./components/pages/Contact/Contact";

const Application = () => {
	const router = createBrowserRouter([
		{
			path: ROUTES.HOME,
			element: (
				<NoGaurd>
					<Homelayout />
				</NoGaurd>
			),
			errorElement: <ErrorPage />,
			children: [
				{
					index: true,
					element: <Home />,
				},
				{
					path: ROUTES.HOME,
					element: <ContentLayout />,
					children: [
						{
							path: ROUTES.BACKGROUND.SAINTS,
							element: <Saints />,
						},
						{
							path: ROUTES.BACKGROUND.NIRMALAS,
							element: <Nirmalas />,
						},
						{
							path: ROUTES.BACKGROUND.NIRMAL_BHEKH,
							element: <Bhekh />,
						},
						{
							path: ROUTES.TEACHINGS.TEACHINGS_OF_BABA_BASANT_JI,
							element: <Teachings />,
						},
						{
							path: ROUTES.INTRODUCTION.ABOUT,
							element: <AboutUs />,
						},
						{
							path: ROUTES.INTRODUCTION.JOHLAN_VILLAGE,
							element: <Johlan />,
						},
						{
							path: ROUTES.INTRODUCTION.NAME_OF_KUTIYA,
							element: <Name />,
						},
						{
							path: ROUTES.INTRODUCTION.MAIN_BUILDINGS,
							element: <Buildings />,
						},
						{
							path: ROUTES.INTRODUCTION.GURU_KA_LANGAR,
							element: <Langar />,
						},
						{
							path: ROUTES.INTRODUCTION.DOCTRINES,
							element: <Doctrines />,
						},
						{
							path: ROUTES.LIFE_SKETCHS.LIFE_SKETCH,
							element: <LifeSketch />,
						},
						{
							path: ROUTES.LIFE_SKETCHS.LINEAGE,
							element: <Lineage />,
						},
						{
							path: ROUTES.LIFE_SKETCHS.ANNIVERSARY_DATES_OF_SAINTS,
							element: <AnniversaryDates />,
						},
						{
							path: ROUTES.TEACHINGS.TEACHINGS_OF_BABA_BASANT_JI,
							element: <Teachings />,
						},
						{
							path: ROUTES.TEACHINGS.LOVE_FOR_GURBANI,
							element: <LoveforGurbani />,
						},
						{
							path: ROUTES.TEACHINGS.LOVE_AND_TEACHINGS,
							element: <LoveAndTeachingsforAll />,
						},
						{
							path: ROUTES.TEACHINGS.SWEET_SERMONS,
							element: <SweetSermons />,
						},
						{
							path: ROUTES.TEACHINGS.PRINICIPLES,
							element: <PrinciplesforGurshikhPreaching />,
						},
						{
							path: ROUTES.TEACHINGS.MAKING_EARTH_LITTLE_PURE,
							element: <MakingEarthPure />,
						},
						{
							path: ROUTES.PUBLICATIONS.NIRMAL_BASANT,
							element: <NirmalBasantBahar />,
						},
						{
							path: ROUTES.PUBLICATIONS.STORIES,
							element: <Stories />,
						},
						{
							path: ROUTES.GALLERY.PHOTOS,
							element: <Photos />,
						},
						{
							path: ROUTES.GALLERY.AUDIO,
							element: <Audio />,
						},
						{
							path: ROUTES.GALLERY.VIDEOS,
							element: <Videos />,
						},
					]
				},
				{
					path: ROUTES.CONTACT,
					element: <Contact />,
				},
			],
		},
		{
			path: "*",
			element: <NotFoundPage />,
		},
	]);
	return (
		<>
			<RouterProvider router={router} />
		</>
	);
};

export default Application;
