import React, { useState } from 'react';
import { Modal } from 'react-bootstrap';
import ImageGallery from "react-image-gallery";
import { CloseIcon } from '../../../assets/icons/icons';
import img1 from "../../../assets/images/gallery/1.jpg";
import img2 from "../../../assets/images/gallery/2.jpg";
import img3 from "../../../assets/images/gallery/3.jpg";
import img4 from "../../../assets/images/gallery/4.jpg";
import img5 from "../../../assets/images/gallery/5.jpg";
import styles from "./Gallery.module.scss";

const Photos = () => {
	const [show, setShow] = useState(false);
	const [startIndex, setStartIndex] = useState(0);
	const images = [
		{
			original: img1,
			thumbnail: img1,
			title: "Sriman 108 Sant Avtar Singh Ji, paying obeisance at Kutiya on 07-03-2021 Night Samagam, accompanied By Sant Jeet Singh ji and Sant Gurmeet Singh JI",
		},
		{
			original: img2,
			thumbnail: img2,
		},
		{
			original: img3,
			thumbnail: img3,
		},
		{
			original: img4,
			thumbnail: img4,
		},
		{
			original: img5,
			thumbnail: img5,
		},
	];

	return (
		<>
			<ul className={styles.gallery}>
				{
					images.map((item, index) => (
						<li key={index}>
							<div>
								<img onClick={() => { setShow(true); setStartIndex(index) }} src={item.thumbnail} alt="" />
								<p>{item.title}</p>
							</div>
						</li>
					))
				}
			</ul>
			<Modal
				show={show}
				onHide={() => setShow(false)}
				centered
				className={styles.gallery_modal}
				backdropClassName='gallery_modal'
			>
				<button onClick={() => setShow(false)} className={styles.close_btn}><CloseIcon /></button>
				<ImageGallery useBrowserFullscreen startIndex={startIndex} items={images} />
			</Modal>
		</>
	)
}

export default Photos