import React from 'react'
import ReactPlayer from 'react-player'
import styles from "./Gallery.module.scss";

const Videos = () => {
    const videos = [
        {
            src: "https://www.youtube.com/watch?v=z0fPau_sTIg",
        },
        {
            src: "https://www.youtube.com/watch?v=j8HBLi8aQBg",
        }
    ]
    return (
        <div>
            <ul className={styles.video_gallery}>
                {
                    videos.map((item, index) => (
                        <li key={item.src}>
                            <div>
                                <ReactPlayer width={"100%"} controls url={item.src} />
                            </div>
                        </li>
                    ))
                }
            </ul>
        </div>
    )
}

export default Videos