import React from "react";
import { Container } from "react-bootstrap";
import styles from "./Footer.module.scss";
import logo from "../../../assets/logos/logo.png"
import textLogo from '../../../assets/logos/text-logo.png'
import { ROUTES, SOCIAL_MEDIA } from "../../../utils/constants";
import Button from "../Button/Button";
import { NavLink, useNavigate } from "react-router-dom";

const Footer = () => {
	const navigate = useNavigate();
	return (
		<>
			<footer className={styles.footer}>
				<Container>
					<div className={styles.footer_in}>
						<div className={styles.logo}>
							<img src={logo} alt="nirmal kutia johlan" />
							<img src={textLogo} alt="nirmal kutiya johlan" />
						</div>
						<div className={styles.right}>
							<Button className={styles.contact_btn} onClick={() => navigate(ROUTES.CONTACT)}>Contact Us</Button>
							<ul>
								<li>
									<NavLink to={ROUTES.GALLERY.VIDEOS}>Videos</NavLink>
								</li>
								<li>
									<NavLink to={ROUTES.GALLERY.PHOTOS}>Photos</NavLink>
								</li>
								<li>
									<NavLink to={SOCIAL_MEDIA.INSTAGRAM}>Instagram</NavLink>
								</li>
								<li>
									<NavLink to={SOCIAL_MEDIA.YOUTUBE}>Youtube</NavLink>
								</li>
								<li>
									<NavLink to={SOCIAL_MEDIA.FACEBOOK}>Facebook</NavLink>
								</li>
							</ul>
						</div>
					</div>
					<div className={styles.footer_nav}>
						<ul>
							<li>
								<NavLink to={ROUTES.HOME}>Home</NavLink>
							</li>
							<li>
								<NavLink to={ROUTES.BACKGROUND.SAINTS}>The Saints</NavLink>
							</li>
							<li>
								<NavLink to={ROUTES.BACKGROUND.NIRMALAS}>Nirmalas</NavLink>
							</li>
							<li>
								<NavLink to={ROUTES.BACKGROUND.NIRMAL_BHEKH}>Nirmal Bhekh</NavLink>
							</li>
							<li>
								<NavLink to={ROUTES.TEACHINGS.LOVE_FOR_GURBANI}>Love For Gurbani</NavLink>
							</li>
						</ul>
						<p>Copyright  {(new Date()).getFullYear()} | All Rights Reserved.</p>
					</div>
				</Container>
			</footer>
		</>
	);
};

export default Footer;
