import React from 'react'

const Lineage = () => {
    const data = [
        {
            title: "Sant Baba Kaladhari ji",
            content: "The descendant of Sahib Sri Guru Nanak Dev ji",
        },
        {
            title: "Sant Baba Ajeet Singh ji",
            content: "The descendant of Sahib Sri Guru Nanak Dev ji",
        },
        {
            title: "Sant Baba Sahib Singh ji Bedi",
            content: "The descendant of Sahib Sri Guru Nanak Dev ji",
        },
        {
            title: "Sant Baba Bir Singh ji",
            content: "of Naurangabad",
        },
        {
            title: "Sant Baba Ram Singh ji ‘Almast’",
            content: "of Naurangabad",
        },
        {
            title: "Sant Baba Karam Singh ji",
            content: "of Hoti Mardan",
        },
        {
            title: "Sant Baba Harnam Singh ji",
            content: "of Jiyan",
        },
        {
            title: "Sant Baba Basant Singh ji",
            content: "of Nirmal Kutiya Johalan",
        },
        {
            title: "Sant Baba Gian Singh ji",
            content: "of Nirmal Kutiya Johalan",
        },
        {
            title: "Sant Baba Harbhajan Singh ji ‘Virakat’",
            content: "of Nirmal Kutiya Johalan",
        },
        {
            title: "Sant Baba Jit Singh ji",
            content: "Present Head of Nirmal Kutiya Johalan",
        },
    ]
    return (
        <>
            <h2>Lineage</h2>
            <h3 className='mb-5'>LINEAGE OF THE SANT SECT OF NIRMAL KUTIYA JOHALAN</h3>
            <ul className="lineage">
                {
                    data.map(item => (
                        <li>
                            <h5>{item.title}</h5>
                            <p>{item.content}</p>
                        </li>
                    ))
                }
            </ul>
        </>
    )
}

export default Lineage