import React from "react";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import { Link, useNavigate } from "react-router-dom";
import logo from "../../../assets/logos/logo.png";
import { ROUTES } from "../../../utils/constants";
import Button from "../Button/Button";
import styles from "./Header.module.scss";
import Sidebar from "./Sidebar/Sidebar";

const Header = () => {
	const navigate = useNavigate();
	return (
		<>
			<div className={styles.header}>
				<Navbar expand="lg" className={styles.header_bg}>
					<Container>
						<Link className={styles.logo} to={ROUTES.HOME}><img src={logo} alt="nimral kutiya johlan" /></Link>
						<Button onClick={() => navigate(ROUTES.CONTACT)} text="Contact Us" className={`${styles.contact} order-xl-1 ms-auto me-xl-0 me-4`} />
						<Sidebar />
					</Container>
				</Navbar>
			</div>
		</>
	);
};

export default Header;
