import React from "react";
import nirmal from "../../../../assets/images/bhekh.jpg";

const Bhekh = () => {
	return (
		<>
			<h2>Nirmal Bhekh</h2>
			<div className="img mb-5">
				<img src={nirmal} alt="nirmal_img" />
			</div>
			<p>
				Nirmalas had a specific job to perform. The propriety
				set by Sahib Sri Guru Nanak Dev ji and suitable decency
				and civility added by Sahib Sri Guru Gobind Singh ji led
				to the establishment of different Deras by the Nirmalas,
				in different parts of Punjab as well as outside the
				state. This happened when the chief of one Dera asked a
				particular Chela who according to him had accomplished
				scholarly level and attained spiritual level, and could
				now make a new beginning on his own. This Chela then
				moved out of the Dera and established with his own
				Bhakti a new Dera at a different place.
			</p>
			<p className="my-5">
				Two types of Sadhus of the Nirmala Bhekh (SECT) took
				shape. One who remained in the Dera and the other who
				visited different places to preach.
			</p>
			<p>
				Many Deras engrossed themselves in teaching Gurumukhi,
				explaining the history of the Sikhs, spreading the
				teachings of the ten Gurus; discussing Brahma Vichar and
				reminded the devotees about the wisdom as observed in
				Guru Granth Sahib. Some of the Deras took up the
				challenge of encouraging their devotees to get baptized
				and accept the sole authority established by the ten
				Gurus.
			</p>
			<p className="my-5">
				Whatever the objectives of a Nirmala Dera, the root
				doctrines and canons were followed by each one of them.
				A few of these are listed below:
			</p>
			<p>
				Whatever the objectives of a Nirmala Dera, the root
				doctrines and canons were followed by each one of them. A
				few of these are listed below:
			</p>
			<p className="my-5">
				Nirmalas had a specific job to perform. The propriety set by
				Sahib Sri Guru Nanak Dev ji and suitable decency and
				civility added by Sahib Sri Guru Gobind Singh ji led to the
				establishment of different Deras by the Nirmalas, in
				different parts of Punjab as well as outside the state. This
				happened when the chief of one Dera asked a particular Chela
				who according to him had accomplished scholarly level and
				attained spiritual level, and could now make a new beginning
				on his own. This Chela then moved out of the Dera and
				established with his own Bhakti a new Dera at a different
				place.
			</p>
			<p>
				Two types of Sadhus of the Nirmala Bhekh (SECT) took shape.
				One who remained in the Dera and the other who visited
				different places to preach.
			</p>
			<p className="my-5">
				Many Deras engrossed themselves in teaching Gurumukhi,
				explaining the history of the Sikhs, spreading the teachings
				of the ten Gurus; discussing Brahma Vichar and reminded the
				devotees about the wisdom as observed in Guru Granth Sahib.
				Some of the Deras took up the challenge of encouraging their
				devotees to get baptized and accept the sole authority
				established by the ten Gurus.
			</p>
			<p>
				Whatever the objectives of a Nirmala Dera, the root
				doctrines and canons were followed by each one of them. A
				few of these are listed below:
			</p>
			<ol className="my-5">
				<li>
					Complete love and devotion of God,
					<br />
					Belief only in the teachings of the ten Sikh Gurus and
					Guru Granth Sahib.
				</li>
				<li>
					Belief that the ‘MOOL MANTRA’ or the ‘ORIGINAL HYMN’
					was heard by Guru Nanak Dev ji from the AKAL PURKH and
					therefore considered it their privilege to be blessed with
					the ‘MOOL MANTRA’ and other teachings from their mentor, a
					live Guru.
				</li>
				<li>
					After the baptism (Amrit Sanchar), they, in reverence
					touch the feet of the chief saint, since it is with his
					blessings that they have attained this state. Touching the
					feet and bowing on the feet of their live Preceptor is a
					daily tradition.
				</li>
				<li>
					Consider it auspicious to establish religious places
					(Gurudwaras), where any of the ten Gurus had the occasion
					to visit and had some historical significance. They also
					establish Gurudwaras at places visited by their Preceptor
					and considered by them as pious places.
				</li>
				<li>
					They don saffron or white clothes on their body. The
					robe is called GATTI.
				</li>
				<li>
					Teachings of Guru Granth Sahib is foremost, but are
					considered scholars of Vedas and Puranas.
				</li>
			</ol>
			<p>
				The NIRMALAS practice celibacy and are devoted to scriptural
				and philosophical study. Their contribution towards the
				preaching of Sikh doctrine and production of philosophical
				literature in Sanskrit, Braj, Hindi and Punjabi is
				considerable.
			</p>
			<p  className="my-5">
				Nirmala scholars along with their disciples attend religious
				fairs at prominent pilgrimage centers and join in the
				processions taken out at the time of various Gurpurabs.
			</p>
			<p>
				History tells us that when Guru Gobind Singh ji established
				the ‘KHALSA PANTH’ by baptizing the Sikhs with ‘AMRIT’. Many
				Nirmala saints at that time considered it their privilege to
				also get baptized. So much so that when the news of the
				‘KHALSA PANTH spread far and wide, Nirmala saints from far
				off places like those living in Kaanshi, Varanassi etc, came
				to Anandpur Sahib and requested the Tenth Guru to bless them
				with the ‘AMRIT’.
			</p>
			<p className="mt-5">
				History tells us that when Guru Gobind Singh ji established
				the ‘KHALSA PANTH’ by baptizing the Sikhs with ‘AMRIT’. Many
				Nirmala saints at that time considered it their privilege to
				also get baptized. So much so that when the news of the
				‘KHALSA PANTH spread far and wide, Nirmala saints from far
				off places like those living in Kaanshi, Varanassi etc, came
				to Anandpur Sahib and requested the Tenth Guru to bless them
				with the ‘AMRIT’.
			</p>
		</>
	);
};

export default Bhekh;
