import React from 'react'

const PrinciplesforGurshikhPreaching = () => {
	return (
		<>
			<h2>
				Principles of Gurusikh Preaching
			</h2>
			<p>
				Sant Baba Gian Singh ji Maharaj had reached a very high spiritual level. His words, however brief, were instrumental in changing the minds of individuals. The individuals for whom the words were uttered got really impressed and their life changed. They became seekers of the ambrosial nectar, started reading and understanding Gurbani and adopted the Sikh ways of life.
			</p>
			<p className="my-5">
				Sant Baba ji prescribed the following principles at Nirmal Kutiya Johalan, for the Gursikhs/sangat to follow:
			</p>
			<ol>
				<li>
					Full respect should be paid to Sri Guru Granth Sahib at Nirmal Kutiya Johalan.
				</li>
				<li>
					Every Gursikh or Sadhu of Nirmal Kutiya Johalan will sleep with his bedding on the floor and not use any cot or bed.
				</li>
				<li>
					Everyday the PARKASH of Sri Guru Granth Sahib will be undertaken at 4.00 am in the morning. Everybody in the Kutiya will therefore get up early and after taking his bath will recite the NITNEM BANIS after paying homage to Sri Guru Granth Sahib.
				</li>
				<li>
					Every Sunday and also on every SANGRAND, a congregation will be held after the BHOG of Sri AKHAND PATH. Every preacher, priest, Ragi etc. at the Kutiya should be a baptized Sikh, following the Sikh Code of Conduct and regular in reciting the NITNEM BANIS. At the time of attendance, he would have a flowing beard and would be in simple Gursikh dress – Kurta or Shirt and Pyjama.
				</li>
				<li>
					The persons preaching or singing the Holy Hymns would limit their discourses to only Gurbani, history of the Sikhs and praise of the Gurus. There would be no discussion of politics in the Kutiya.
				</li>
				<li>
					Every Sangrand and on any other appropriate day, the PANJ PIARAS (The Five Beloved Sikhs) would prepare the ambrosial nectar of the two edged sword and the seekers baptized with the ambrosial nectar.
				</li>
				<li>
					Baptized Sikh ladies would only undertake the service in the langar. ‘Guru ka langar’ will be served in Pangats and the sangat will recite ‘SATNAM WAHEGURU’ during the distribution of langar. Baptized Sikhs will serve the langar with their mouths covered with a cloth. The hands of the sangat will be washed after the sangat finishes eating. Parshad will be served to the sangat and then leave will be taken from the sangat.
				</li>
				<li>
					The sangat will stand in respect whenever the DEG of KARAH PARSHAD is brought after preparation in the kitchen and placed near Sri Guru Granth Sahib. The attendants will serve the DEG with their mouth covered with a cloth.
				</li>
			</ol>
			<p className="mt-5">
				These main principles laid down by Sant Baba Gian Singh ji Maharaj are followed in Nirmal Kutiya Johalan. These have become instrumental in disciplining the people who attend the congregations at Nirmal Kutiya Johalan. People respect and follow these ground rules. This is the main reason that whosoever visits Nirmal Kutiya Johalan finds that the Code of Conduct prescribed by Sri Guru Nanak Dev ji and Sri Guru Gobind Singh Sahib ji is being followed and the MARYADA is intact here.

			</p>
		</>
	)
}

export default PrinciplesforGurshikhPreaching