import React from 'react'
import img1 from "../../../assets/images/making-earth-a-better-place/earth_large.png";
import img2 from "../../../assets/images/making-earth-a-better-place/earth_2.png";
import img3 from "../../../assets/images/making-earth-a-better-place/earth_3.png";
import img4 from "../../../assets/images/making-earth-a-better-place/earth_4.png";
import img5 from "../../../assets/images/making-earth-a-better-place/earth_5.png";
import img6 from "../../../assets/images/making-earth-a-better-place/earth_6.png";
import img7 from "../../../assets/images/making-earth-a-better-place/earth_7.png";

const MakingEarthPure = () => {
	return (
		<>
			<h2>
				Making Earth a Little Pure
			</h2>
			<div className="img mb-5">
				<img src={img1} alt="" />
			</div>
			<h3>
				CHANGING CLIMATE IN THE WORLD
			</h3>
			<p>
				Global warming is a reality. Disruption in monsoon patterns, shift in ocean currents, and major droughts have left many a homeless. The world is fast becoming a hugely confusing place for anyone to live a more environmentally friendly life. Change of climate is the most urgent and wide ranging environmental threat which is mostly the doing of humans themselves. It has been reported in many journals that human-induced warming of the climate is widespread.
			</p>
			<p className="my-5">
				Every activity of humans is responsible for releasing greenhouse gases into the atmosphere. While in the atmosphere, these gases absorb heat that’s radiated by Earth. The gases do release some part of that heat to space but that is quite less, resulting in a warmer atmosphere.
			</p>
			<p>
				Humans rely on energy provided by fossil fuels i.e. coal, oil and natural gas. These fuels create carbon dioxide when they’re burned, which in turn powers everything from cars to the lighting in our homes. Carbon dioxide and other greenhouse gases like methane and nitrous oxide are also released by deforestation, agriculture, rotting waste and various industries.
			</p>
			<h3 className="my-5">
				ORGANIC FARMING AT NIRMAL KUTIYA JOHALAN
			</h3>
			<div className="img mb-5">
				<img src={img2} alt="" />
			</div>
			<p>
				Industrialized agriculture thrashes the land, and diminishes its soil life to the point where it can no longer function to convert available organic matter into soil fertility. Productivity begins to wane, and attempts to bolster it with increasing chemical inputs fails miserably.
			</p>
			<p className='my-5'>
				Feeding the soil with organic matter instead of ammonia and other synthetic fertilizers has proven to increase nutrients in produce, with higher levels of vitamins and minerals found in organic food.
			</p>
			<p>
				Sriman Sant Baba Basant Singh ji impressed the farmers, with self example, to go in for natural farming, now known as organic farming. He did not want people to use fertilizers in the soil or pesticides on the crops. Organic farming does not incur the use of expensive agrichemicals. The greater resistance of the crops to pests and the diseases save farmers significantly in expensive insecticides, fungicides and other pesticides. His mission was carried on by his descendants in the true spirit and keeping a small part of the earth clean and green. At present also only organic farming is being undertaken on the agriculture land.
			</p>
			<p className='my-5'>
				The surroundings of Nirmal Kutiya Johalan have a peaceful, clean and pure environment. During the times of Sriman Sant Baba Basant Singh ji, whenever some one approached the Kutiya either from the Railway Station or from the Road, he first observed the silence in the atmosphere. The sound of a running well quite a distance away could easily be heard. The chirping of the birds could be distinctly listened to. Apart from this the purity around could also be easily observed. One was indeed approaching a serene and divine atmosphere.
			</p>
			<div className="img mb-5">
				<img src={img3} alt="" />
			</div>
			<p>The descendants of Sant Baba Basant Singh ji have been aware of the global issue of climate change. The present head of Nirmal Kutiya Johalan, Sant Baba Jit Singh ji is also contributing a green footprint of his own during these days. By using organic fertilizers (e.g. compost, animal manure, green manure) the groundwater is being saved from pollution from synthetic fertilizers and pesticides.</p>
			<p className='my-5'>It is an established fact that by not using the fertilizers and pesticides, the retentive abilities of the soil for nutrients and water gets enhanced. Sant Baba ji says that such techniques also play an important role in the control of soil erosion.</p>
			<p>The organic farms are full of animal, bird and insect activity. Species of many birds vanished from the nearby areas but can be seen here because of organic practices.</p>
			<p className='my-5'>Consuming organic food ensures that one is saved from health harming chemicals such as pesticides, fungicides and herbicides. The tastiness of food is a function of the quality of nutrition that the plant itself has enjoyed.</p>
			<p>The impact of organic agriculture include soil forming and conditioning, soil stabilization, waste recycling, nutrients cycling, predation, pollination and habitats.</p>
			<h3>GREENERY AND PLANTATION OF TREES</h3>
			<div className="img mb-5">
				<img src={img4} alt="" />
			</div>
			<p>
				In recent years, due to greed and ignorance, we have failed to nurture and develop nature. Everywhere, the beauty of green is being replaced by the grey of concrete. With the loss of forests and green cover throughout the planet, more and more species of flora and fauna are becoming extinct.
			</p>
			<p className='my-5'>Just like employment, education and healthcare, pure air and green cover is the need of every individual.</p>
			<p>It is an established fact that there are a number of environmental benefits relating to the presence of trees.</p>
			<ol className='my-5'>
				<li> Trees are like the lungs of the planet. They breathe in carbon dioxide and breathe out oxygen.</li>
				<li>Trees and shrubs remove smoke, dust and other pollutants from the air.</li>
				<li> Trees in a parking lot can reduce on-site heat buildup, decrease runoff and enhance night time cool downs.</li>
				<li> Trees reduce the amount of storm water runoff, which reduces erosion and pollution in our waterways and may reduce the effects of flooding.</li>
				<li> Many species of wildlife depend on trees for habitat. Trees provide food, protection, and homes for many birds and mammals.</li>
			</ol>
			<p>
				The planting of trees therefore represents an effective means to fight climate change
			</p>
			<div className="img my-5">
				<img src={img5} alt="" />
			</div>
			<p>Trees purify the air by a) removing dust particles from the air, b) absorb compounds such as nitrogen oxide, sulphur dioxide and c) by breaking down various noxious compounds into less harmful elements during periods of smog. These dust particles are also the cause of numerous respiratory problems. Carried by the wind, these fine particles attach themselves to the surface of the leaves and are then washed off by the rain;</p>
			<p className='my-5'>Trees provide shade and reflect some of the sun’s rays, which would otherwise be absorbed by concrete and asphalt.</p>
			<p>Many people must have observed Holy saints in penance and meditation. Many people would have indulged in a discussion about the divine powers of a Holy saint. But people must not have heard much about those saints who think about the society and do something to improve the living conditions of ordinary people.</p>
			<p className='my-5'>Sant Baba Basant Singh ji Maharaj, a Braham Giani, did many things for the common man. He performed tasks that benefited not only the persons living during his time but for future generations. He is remembered by the society for the works he had performed for the welfare and convenience of the public.</p>
			<p>The present approach road to the Kutiya used to be covered with hedges and bushes. The entire area was sandy. Many deep gorges and sand dunes covered the path. No government department was willing to take action for the construction of a road. It was an arduous task. People living in the nearby villages faced a lot of difficulty reaching their homes, but nothing was being done about it.</p>
			<div className="img my-5">
				<img src={img6} alt="" />
			</div>
			<p>It was really commendable of Sant Baba ji to have taken the responsibility of leveling the path and construction of a road not only till the Kutiya but extended it till the Bolina Doaba Railway Station. The approach road to Nirmal Kutiya Johalan is now decked on both sides with trees planted by the Holy Saints of the Kutiya. Greenery in the fields on both sides is enhanced with the presence of these trees. These trees although are on Government land yet are being nurtured by the Sadhus of the Kutiya under the instructions of the present head of the Kutiya, Sant Baba Jit Singh ji. The left and right side of the entrance gate of the Kutiya has two small beautiful gardens. Across the road in front of the entrance gate is another big park developed by the Kutiya. Just next to it is the area for parking cycles, two wheelers and four wheelers. This area is also green and is covered with trees to shade the vehicles. A part of this portion is used for shoes storage during functions at the Kutiya.</p>
			<p className='my-5'>Inside the Kutiya there are many mango trees and other trees. The path inside is lined with trees and plantation areas for flowers on both sides. The corral for the animals of the Kutiya also has trees for shade.</p>
			<p>When one enters the smaller gate towards the Sachkhand and Sheesh Mahal buildings, a huge mango tree and a Keno fruit tree welcomes the devotees. There is another green area full of flowers behind the Sachkhand building.</p>
			<p className='my-5'>The atmosphere in the Kutiya is already tranquil and pure with the Bhakti of the Holy Saints and this gets augmented with the poise of green trees.</p>
			<h3>SANITATION</h3>
			<div className="img my-5">
				<img src={img7} alt="" />
			</div>
			<p>In sanitation, there is an element of “all or nothing” in terms of the benefits. The full health, environmental and other dividends for a community only come into play when everyone has access to adequate. Public toilets are only one step to meet the sanitation needs of low-income urban communities with some advantages and disadvantages over communal latrines. They satisfy an important sanitation need in all communities. Keeping in view the natural calls of the devotees, the Holy Saints have constructed toilets and bath rooms with Indian and English WC’s separately for men and women. Complete cleanliness of these areas is ensured.</p>
			<p className='my-5'>There are many instances where the Saints of Nirmal Kutiya Johalan have been instrumental in making the earth a cleaner place. One of these relating to their awareness of the issue of hygiene for good health of the people is cited here. There used to be dirty pond in the village of Johal. It was being used to dump garbage. Humans used it for excreta too. Bees, insects and mosquito’s were breeding on it. Sant Baba ji was requested to do something about the filthy atmosphere in the village. Sant Baba ji visited the place and took on him to fill the pond. With his instructions the sangat covered it in a short time. Under the supervision of Sant Baba ji, a building was got constructed on this place. This place is now being used as a Baraat Ghar (Banquet palace) for social events and gatherings by the villagers.</p>
			<p>Another recent development has been of the devotees of the area approaching the Holy Saint for the construction of a Yaadgaari Gate at the turning on Jalandhar Hoshiarpur road towards the approach road leading to the Kutiya. The devotees wanted to preserve the memory of Sant Baba Basant Singh ji’s contribution to the society in initiating the construction of the road leading to many villages. This memorial gate, though still to be completed, yet has been seen to shelter people waiting for the buses, two wheeler drivers during rain and has also become a resting place for the pedestrians.</p>
			<p className='mt-5'>The Holy Saints of Nirmal Kutiya Johalan are making a difference to the planet Earth with a small contribution of their own. Their awareness of environmental issues has influenced their taking up the activities mentioned above.</p>
		</>
	)
}

export default MakingEarthPure