import React from 'react'

const LoveAndTeachingsforAll = () => {
    return (
        <>
            <h2>Love and Teachings for All</h2>
            <ol>
                <li>
                    Such was the splendour of the personality of Sant Baba Basant Singh ji Maharaj that many adherents of other religions, most of whom were Hindus and Muslims also became his followers. Sant Baba ji’s blessings and love was for all humans irrespective of religion, class or their status. Nambardar Umardin of village Chohak Kalan considered Sant Baba ji as his ‘Pir’ and requested him to bless the village with his Holy feet. Sant Baba ji reached the village and asked about the owner of the house he passed. In reply he was told the name of the house owner. Sant Baba ji blessed the house of Umardin. Umardin was very happy as his ‘Pir’ had placed his Holy feet in the house. He told Sant Baba ji that he was not shortage of any material comforts and had everything but in spite of seven marriages, he did not have any issue. He requested Sant Baba ji to bless him with a son. Sant Baba ji said, “Your house is very beautiful. I have passed the houses in the village and every house has a owner. I have not seen the house of ALLAH."
                    <br /><br/>
                    Umardin immediately understood the depth of Sant Baba ji’s words. He constructed a Masjid in the village for the villagers to offer their daily Namaz. The result of this was felt by Umardin in his life. All the Begums of Umardin were blessed with a son each. Umardin celebrated the birth of each son by taking sweets for the ‘Pir’ with a musical band performing on the way to the Kutiya.
                </li>
                <li>
                    Sant Baba Basant Singh ji Maharaj used to go out of the Kutiya for a daily walk with some of his followers. He walked past the nearby villages. Bhagu, a drug addict made a living by grazing his goats. He used to often see Sant Baba ji but never came near him. He used to bow his head in reverence from a distance. One day Sant Baba ji asked a devotee to go and bring Bhagu to prune a tree. Bhagu came and after pruning the tree was leaving the place. Sant Baba ji immediately asked the devotee, “Please request Sant ji to sit on the mat.”<br /><br/>
                    The devotee could see Bhagu only in the vicinity and was astonished to hear the words of Sant Baba ji. Sant Baba ji on seeing his plight looked at Bhagu and again said, “Please ask Sant ji standing there to sit on a mat. Serve him with milk and snacks and then take leave of him.” All the villagers knew Bhagu as a drug addict. But that day he came straight to his home from the Kutiya. He told his mother that he had become a saint and that his goats and mace, should now be looked after by her. The mother was not astonished at these words as she felt that her son must have taken an extra doze of drugs that day. However Bhagu left the house and went away. Bhagu was not to be found after that day and the relatives considered him dead. One day the devotee of Sant Maharaj ji was in Hoshiarpur for some work when his name was called by a graceful Sadhu. He went near the Sadhu and respectfully said that he had not recognized the Sadhu. The Sadhu smilingly said, “You are the one who had taken me to the Kutiya of Sant Baba Basant Singh ji Maharaj who then converted me into a saint. I am Bhagu.” <br /><br/>
                    Bhagu was now known as Sant Bhagat Singh ji. He had a name in the nearby villages of Hoshiarpur. He had a good following as whatever he said came out to be true. Once he came to Nirmal Kutiya Johalan and sought refuge in the feet of Sant Baba Basant Singh ji Maharaj. He respectfully and full of humility said, “Maharaj ji! Please cover all my misdeeds of this life and my previous ones with your grace so that no one would have any knowledge about my past.” <br /><br/>
                    Sant Baba ji asked Sant Baba Gian Singh ji Maharaj, “Please cover the body of Sant Bhagat Singh ji with his shawl.”
                    Sant Baba ji then took off his own shawl he was covered in and handed it over to Sant Baba Gian Singh ji. Sant Bhagat Singh understood the meaning of this Holy Blessing and knew that his prayer had been answered.
                </li>
                <li>
                    The Muslims of village Dhilwan did not permit the devotees to take out a Nagar Kirtan on the occasion of birth anniversary of Bhagat Ravidas ji. The devotees came to the refuge of Sant Baba ji, who with his charm impressed the Muslims and found an amicable solution.
                </li>
                <li>
                    Many individuals from other communities embraced Sikhism.

                </li>
                <li>
                    Apart from the Nirmalas coming from other centers, sadhus of even other sects would come to seek his guidance.
                </li>
                <li>
                    Villagers who habitually wore loose wraparound cloth around the loins, dyed their hair or trimmed or cut it, were transformed by him though initiation with the ambrosial nectar of the two edged sword.
                </li>
                <li>
                    The word of this ascetic sadhu carried such force that minds were instantaneously reformed, and the fortunate ones would immediately tread on the Guru’s path.
                </li>
                <li>
                    He taught people how to come out of their superstitions and fallacies and follow the simple and true path of a dedicated Sikh.
                </li>
                <li>
                    He was a self-denying sadhu and no one could find a gap between what he preached and what he practiced. This was the reason why all from constable to SSP or even IG of the Police department, soldier to General from the Army, politicians, government officials, farmers big or small, Doctors, Engineers etc, who once had audience with him were inevitably drawn to the Gurbani and encouraged to become baptized Sikhs. No matter how high the rank of an officer, this detached sadhu considered him a part of the sangat.

                </li>
            </ol>
        </>
    )
}

export default LoveAndTeachingsforAll