import React from "react";
import langar from "../../../../assets/images/langar.jpg";
import langarNew from "../../../../assets/images/langarNew.jpg";
const Langar = () => {
  return (
    <>
      <h2>Guru ka Langar</h2>
      <div className="img mb-5">
        <img src={langar} alt="langar" />
      </div>
      <p>
        The concept of ‘Guru Ka Langar’ in Sikhism has a very special
        place. The Sikh Gurus contribution to the society has been
        tremendous but ‘Guru Ka Langar’ became an integral part of
        Sikh creed.
      </p>
      <p className="my-5">
        It was inspired by Sahib Sri Guru Nanak Dev ji Maharaj, the
        first Guru of the Sikhs, when he served food to wandering holy
        men from the money given to him by his father to strike a
        gainful bargain. For him, this service to the needy was more
        meaningful than capitalizing the amount for personal profit.
      </p>
      <p>
        Guru Nanak, as did Guru Angad, toiled in the fields to provide
        for himself and for his family and to contribute his share to
        the common langar. The Bards, Satta and Balvand applaud Sahib
        Sri Guru Amar Das ji Maharaj through the following words:
      </p>
      <div className="box my-5">
        <p>ਨਿਤ ਰਸੋਈ ਤੇਰੀਐ ਘਿਉ ਮੈਦਾ ਖਾਣੁ || (ਪੰਨਾ ੯੬੮)</p>

      </div>
      <p>
        In spite of rich variety of food served in his langar, Sahib
        Sri Guru Amar Das ji Maharaj ate a simple meal earned by the
        labor of his own hands. Partaking of food in ‘Guru Ka Langar’
        was made a condition for disciples and visitors before they
        could see Sahib Sri Guru Amar Das ji Maharaj. He preached and
        practiced:
      </p>
      <div className="box my-5">
        <p>ਪਹਿਲੇ ਪੰਗਤ ਪਾਛੇ ਸੰਗਤ</p>
        <p className="trnsltn">‘First comes eating together, then meeting together.’</p>
      </div>
      <p>
        This spirit of common sharing and of mutual co-operation and
        service was the underlying principle of the Sikh tradition of
        langar. Sahib Sri Guru Nanak Dev ji Maharaj started a movement
        against the oppression of the high born over the people of
        modest origin; and the ‘Guru Ka Langar’ was an institution
        which was promulgated in this holy journey. The free kitchen
        became the foundation of equality and sorority, among the
        followers of Sikh Gurus.
      </p>
      <p className="my-5">
        ‘Guru ka Langar’ establishes the principle of parity between
        all people of the world regardless of religion, caste, color,
        creed, age, gender or social status. In addition to the ideals
        of equality, the tradition of ‘Guru Ka Langar’ expresses the
        ethics of sharing, cooperation, inclusiveness and oneness of
        all humankind.
      </p>
      <div className="img">
        <img src={langarNew} alt="langar_img" />
      </div>
      <p className="my-5">
        Sriman Sant Baba Basant Singh ji Maharaj performed extreme
        austerities at Nirmal Kutiya Johalan. With Holy hymns of Guru
        Sahib embedded solidly within and reciting the Name of the
        Lord, Sant ji Maharaj made the foundation of the Kutiya
        stable. He preached the love for Gurbani to his devotees. The
        Tenth Guru Sahib’s Nectar of the two edged sword was served to
        the seeker changing their lives for ever. He carried on the
        tradition and importance to ‘selfless service’ so that the
        devotees learnt to become perfect in its performance. After
        him, the discipline was kept to the fore by Sriman Sant Baba
        Gian Singh ji and then by Sriman Sant Baba Harbhajan Singh
        ‘Virakat’ ji. In the present day the old tradition is kept
        afloat by Sant Baba Jit Singh ji, the present head of Nirmal
        Kutiya Johalan.
      </p>
      <p>
        The following rules fixed for the service of ‘Guru Ka Langar’
        at Nirmal Kutiya Johalan are strictly followed:
      </p>
      <ol  className="my-5">
        <li>
          The service of ‘Guru Ka Langar’ can be undertaken by an
          baptized (Amritdhari) Gursikh whose family has adopted the
          Baani(Holy Hymns) and Baana(The Dress Code) of Sahib Sri
          Guru Gobind Singh Sahib ji.
        </li>
        <li>
          Except for the ‘Five Kakkaars’ (Five symbols) blessed by
          Sahib Sri Guru Gobind Singh Sahib ji, the Gursikh should not
          be wearing any ring, necklace, threads, taweet, or any other
          gold or silver ornament.
        </li>
        <li>
          Any service of ‘Guru Ka Langar’ can be undertaken with
          the permission of the Chief of the Kutiya.
        </li>
        <li>
          No sewadar can go to the place where ‘Guru Ka Langar’ is
          kept or is being prepared without the consent of the Sadhu
          incharge for the same.
        </li>
        <li>
          All baptized ladies preparing ‘Guru Ka Langar’ should
          perform their work under strict discipline and while they
          are working, only one designated male sewadar can enter the
          place for giving the items of langar or taking away the
          prepared langar.
        </li>
        <li>
          Desi (Pure) Ghee is only used for preparation of Deg
          (Karah Parshad), parshaade (baked bread), lentils,
          vegetables or any sweets.
        </li>
        <li>
          Only baptized (Amritdhari) Sikhs perform the service of
          preparing sweets during ‘Sangraand’ or during the
          celebration of anniversaries of the Gurudev.
        </li>
        <li>
          Everyone serves with his mouth covered with a cloth.
        </li>
        <li>
          The person undertaking the service keeps on reciting
          Gurbaani in his mind during the service and does not discuss
          anything beyond this service with anyone.
        </li>
        <li>
          At the time of performing langar sewa, the sewadar
          remains humble and does not utter any harsh words. He serves
          the sangat with love.
        </li>
      </ol>
      <p>
        The sangat being served ‘Guru Ka Langar’ is made to sit in
        rows in the langar hall. The hands of the sangat are first
        washed. Utensils are served and then each row is served ‘Guru
        Ka Langar’. During all this time one sewadar is involved in
        the recitation of ‘Satnam Waheguru’ along with the sangat.
        Only when every one has been served, this individual sewadar
        utters ‘Waheguru ji ka Khalsa, Waheguru ji ki Fateh’ and then
        everyone starts eating. Water to wash the hands and mouth is
        served at the end. Parshad of bananas, apples or any other
        fruit or patashas (Sugar puffs) are given before taking leave
        of the sangat.
      </p>
      <p  className="mt-5">
        The discipline, cleanliness and hygiene maintained at Nirmal
        Kutiya Johalan has impressed many. Many high officials, saints
        and dignitaries give example of the discipline maintained for
        ‘Guru ka Langar’ at Nirmal Kutiya Johalan. The devotees while
        bringing ‘Guru Ka Langar’ to the Kutiya, also prepare the same
        in Desi Ghee.
      </p>
    </>
  );
};

export default Langar;
