import React from 'react';
import styles from "./Button.module.scss";

const Button = ({ className, variant, type, children, text, ...rest }) => {
    return (
        <button
            {...rest}
            className={`${styles.button} ${className || ""}`}
            type={type || "button"}
            data-variant={variant}
        >
            {
                children || text
            }
        </button>
    )
}

export default Button