            "The soul of Sant Baba Gian Singh ji Maharaj merged with the eternal Supreme on 6th November 2001. On 18.11.2001 during the bhog ceremony Sant Baba Harbhajan Singh ‘Virakat’ ji Maharaj in the presence of thousands of devotees, was appointed the Chief of Nirmal Kutiya Johalan. Though Virakat ji Maharaj managed the Kutiya for a short period yet he followed the discipline of his Gurdev and encouraged individuals with Gurbani Kirtan and baptizing them.",
            "On 8th March 2002 the soul left the body of Virakat ji and became one with the Eternal. His Holy body was kept in a wooden box and immersed in the deep waters of river Sutlej at Gurdwara Bhabhor Sahib (Nangal).",
