import React, { useEffect, useState } from 'react'
import nbbp2006_1 from "../../../../assets/images/nbbp/nbbp2006-1.jpg";
import nbbp2006_2 from "../../../../assets/images/nbbp/nbbp2006-2.jpg";
import nbbp2007_1 from "../../../../assets/images/nbbp/nbbp2007-1.jpg";
import nbbp2009_1 from "../../../../assets/images/nbbp/nbbp2009-1.jpg";
import nbbp2009_2 from "../../../../assets/images/nbbp/nbbp2009-2.jpg";
import { useParams } from 'react-router-dom';

const d = [
    {
        key: "nirmal-basant-bahar-in-punjabi-2006",
        title: "Nirmal Basant Bahar in Punjabi 2006",
        content: "The Punjabi version of the book ‘Nirmal Basant Bahar’ was first published in 2006. It was printed at the SGPC’s Golden Press in Gurdwara Ram Sar Sahib, Sri Amritsar, where the Holy Sri Guru Granth Sahibs are printed. The book depicts the life and times of Sriman 108 Sant Baba Basant Singh ji Maharaj, the founder of Nirmal Kutiya Johalan. Sant Baba Jit Singh ji, the present Holy saints of Nirmal Kutiya Johalan, took up Sewa of first collecting the incidents of their Gurudev, compiling them and giving shape to the book. The manuscript of the book was presented for review to Sikh literates and highly acclaimed saints of Nirmal Bhekh. The congratulatory messages received from these Sikh dignitaries were published in the book. Three thousand copies of the book were published but all got distributed. There were no charges for the book.",
        gallery: [nbbp2006_1, nbbp2006_2],
    },
    {
        key: "nirmal-basant-bahar-in-punjabi-2007",
        title: "Nirmal Basant Bahar in Punjabi 2007",
        content: "The English translation of the book ‘Nirmal Basant Bahar’ was brought out in 2007. The English version contains some more stories received from the Sangat, after the publication of the Punjabi version in 2006. Some of the incidents have been corrected and some others expanded to lend strength to the teachings of the Holy Saint. As desired by the devotees, a brief life sketch of Braham Giani Sant Baba Harnam Singh ji Maharaj of Jiyan, Gurudev of Sriman Sant Baba Basant Singh ji Maharaj, was also added in this edition.",
        gallery: [nbbp2007_1],
    },
    {
        key: "nirmal-basant-bahar-in-punjabi-2009",
        title: "Nirmal Basant Bahar in Punjabi 2009",
        content: "The second Punjabi edition of the book ‘Nirmal Basant Bahar’ was brought out in late 2009. This edition was thoroughly revised, with further addition of incidents occurred with the Sangat during the times of Sriman Sant Baba Basant Singh ji Maharaj. This edition contained photographs of the latest happenings related to the Holy Saints of Nirmal Kutiya Johalan. Four thousand copies of the book were printed for free distribution.",
        gallery: [nbbp2009_1, nbbp2009_2],
        book: "",
    },
];
const NirmalBasantBahar = () => {
    const { id } = useParams();
    const [data, setData] = useState({});
    useEffect(() => {
        setData(d.filter(item => item.key === id)[0]);
    }, [id])
    return (
        <>
            <h2>{data.title}</h2>
            <div className="d-flex align-items-start justify-content-center gap-4">
                {
                    data.gallery &&
                    data.gallery.map(item => {
                        return (
                            <div key={item} className="mx-0 img">
                                <img src={item} alt="" />
                            </div>
                        )
                    })
                }
            </div>
            <p className="mt-5">{data.content}</p>
        </>
    )
}

export default NirmalBasantBahar