import React from "react";

const Name = () => {
  return (
    <>
                <h2>Name of the Kutiya</h2>
                <p>
                  The discussions about the divinity of Sant Baba Basant Singh
                  ji Maharaj spread wide and far and people were attracted to
                  visit his abode. The Kutiya of Sant Baba ji not only attracted
                  ordinary men, but saints and heads of other religions visited
                  his place of worship as well. The saints from other sects
                  wanted a glimpse of the Holy saint, Sant Baba Basant Singh ji
                  Maharaj. These sadhus were anxious to pay their homage and
                  receive the blessings from the spiritual Master.
                </p>
                <p className="my-5">
                  The holy city of Hardwar has many MATHS and DERAS of Nirmala
                  saints. Sant Baba Basant Singh ji was also a Nirmala saint.
                </p>
                <p>
                  Sant Haridev was one such renowned Nirmala saint, who had his
                  Dera at Hardwar. He came to visit Sant Baba Basant Singh ji at
                  his abode in Johalan. He was really moved on observing the
                  simple living of Sant Maharaj ji. The quiet surroundings were
                  so serene that any person coming even close to the abode could
                  experience the peaceful environment.
                </p>
                <p className="my-5">
                  Sant Haridev ji was welcomed by Sant ji Maharaj. He was given
                  due respect and seated on a white sheet laid on a mat on the
                  floor. Sant Baba ji himself sat on a Murrah. The devotees of
                  Sant ji Maharaj, from the village, served the two saints with
                  food and water.
                </p>
                <p>
                  Sant Haridev ji stayed at the abode for a few days. He kept on
                  surveying the soothing surroundings and tasted the sweet water
                  of the well. He was impressed by the devotion and selfless
                  service of the devotees. He was overwhelmed on seeing the deep
                  pious eyes and the glowing face of Sant ji Maharaj. The
                  chirping of the birds and the sound of the animals was musical
                  and he could sense these beings joining in the daily singing
                  of the Holy Hymns.
                </p>
                <p className="my-5">
                  Sitting in the company of Sant ji Maharaj he quietly raised a
                  question, “Maharaj! What name have you given to this Holy
                  place?”
                </p>
                <p>
                  Sant Baba Basant Singh ji Maharaj, instead of replying asked
                  him, “What name do you suggest for the abode?”
                </p>
                <p className="my-5">
                  Sant Haridev ji commented, “This place has become spotless and
                  smells of freshness due to your presence. Your TAP has made
                  this place peaceful and Holy. The water of the well is clear,
                  cool and sweet. The birds chirp the God’s Name. The place
                  reflects serenity with the presence of Tyagi sadhus. You are
                  NIRMAL both from within and outside. You belong to the NIRMAL
                  sect. You should therefore name this place as ‘NIRMAL DERA’.”
                </p>
                <p>
                  Sant ji Maharaj shook his head in agreement and said, “You
                  have suggested the right name for this place. But let this
                  place be named as ‘NIRMAL KUTIYA’. No matter how big and vast
                  this place becomes tomorrow, it will remind the devotees that
                  the saints of this abode are full of humility.”
                </p>
                <p className="my-5">
                  Sant Haridev ji was very happy to listen to the Holy words of
                  Sant ji Maharaj and said with a smile on his face, “‘NIRMAL
                  SADH’ of ‘NIRMAL KUTIYA’.”
                </p>
                <p>
                  Since that day the abode of Sant ji Maharaj is known as
                  ‘NIRMAL KUTIYA’ and since the land belongs to the village
                  Johal, the name of the village became a part of the name of
                  the abode. It became famous worldwide as ‘NIRMAL KUTIYA
                  JOHALAN’.
                </p>
    </>
  );
};

export default Name;
