import React from "react";
import darbar from "../../../../assets/images/darbar.jpg";
import diwan from "../../../../assets/images/diwan.jpg";
import sachkhand from "../../../../assets/images/sachkhand.jpg";
import sheesh from "../../../../assets/images/sheesh.jpg";
import well from "../../../../assets/images/well.jpg";
import "./Buildings.scss";

const Buildings = () => {
  return (
    <>
      <section className="building_sec">
        <h2>Buildings of Nirmal Kutiya</h2>
        <div className="building_sec_inner_data">
          <img src={sachkhand} alt="sachkhand_img" />
          <p>Sachkhand Building</p>
        </div>
        <div className="building_sec_inner_data">
          <img src={well} alt="well_img" />
          <p>Holy Well</p>
        </div>
        <div className="building_sec_inner_data">
          <img src={sheesh} alt="sheesh_img" />
          <p>Sheesh Mahal</p>
        </div>
        <div className="building_sec_inner_data">
          <img src={diwan} alt="diwan_img" />
          <p>Diwan Hall</p>
        </div>
        <div className="building_sec_inner_data">
          <img src={darbar} alt="well_img" />
          <p>Darbar Hall</p>
        </div>
      </section>
    </>
  );
};

export default Buildings;
